import React from "react"
import {
  GoogleMap,
  useJsApiLoader,
  MarkerClusterer,
  MarkerF,
  Marker,
} from "@react-google-maps/api"
import { vehicleData } from "../../sampleData"
import { ChildComponentProps } from "../Table/TableComponents/TableDataComponent"
const containerStyle = {
  width: "700px",
  height: "400px",
}

const center = { lat: -28.024, lng: 140.887 }

// const locations = vehicleData.map((item) => {
