//     lat: item.coordinate.latitude,
//     lng: item.coordinate.longitude,
//   }
// })

function createKey(location: any) {
  return location.lat + location.lng
}

export const MapComponent: React.FC<ChildComponentProps> = (props) => {
  const { isLoaded } = useJsApiLoader({
    id: "google-map-script",
    googleMapsApiKey: "AIzaSyA5mCRZyI27Avzwzqlum-6KWCXIjty-ASo",
  })

  const [map, setMap] = React.useState(null)

  const onLoad = React.useCallback(function callback(map: any) {
    setMap(map)
  }, [])

  const onUnmount = React.useCallback(function callback(map: any) {
    setMap(null)
  }, [])

  const svgMarker = `data:image/svg+xml;charset=UTF-8;base64,${""}`
  type ClickHandler = (f: any) => (e: google.maps.MapMouseEvent) => void
  const onClickConsole: ClickHandler = (i) => (e) => {
    console.log(i)
  }
  return isLoaded ? (
    <div className='mapStyle'>
      <GoogleMap
        mapContainerStyle={containerStyle}
        center={{
          lng: props.props[0].coordinate.longitude,
          lat: props.props[0].coordinate.latitude,
        }}
        zoom={12}
        onLoad={onLoad}
        onUnmount={onUnmount}
      >
        <MarkerClusterer>
          {(clusterer) => (
            <>
              {props.props.slice(0, 10).map((location: any, index) => (
                <MarkerF
                  key={index}
                  position={{
                    lng: location.coordinate.longitude,
                    lat: location.coordinate.latitude,
                  }}
                  clusterer={clusterer}
                  onLoad={onLoad}
                  icon={svgMarker}
                  onClick={onClickConsole(location)}
                />
              ))}
            </>
          )}
        </MarkerClusterer>
      </GoogleMap>
    </div>
  ) : null
}
