enum VEHICLE_TYPE {
  FREE_NOW = "FREE NOW",
  SHARE_NOW = "SHARE NOW",
}

export type PoiList = {
  id: number
  coordinate: {
    latitude: number
    longitude: number
  }
  state: string
  licencePlate: string
  condition: string
}

export type FreeNowDataType = {
  poiList: PoiList[]
}

export type Placemark = {
  address: string
  coordinates: number[]
  engineType: string
  condition: string
  fuel: number
  state: string
  licencePlate: string
  id: number
}

export type ShareNowDataType = {
  placemarks: Placemark[]
}

export type FinalType = {
  id: number
  statusType: VEHICLE_TYPE
  coordinate: {
    latitude: number
    longitude: number
  }
  state: string
  licencePlate: string
  condition: string
  fuel?: number
  engineType?: string
  address?: string
}

function isFreeNowDataType(arg: any): arg is FreeNowDataType {
  return arg.poiList !== undefined
}

export const dataMapper = (
  input: FreeNowDataType | ShareNowDataType
): FinalType[] => {
  if (isFreeNowDataType(input)) {
    return input.poiList.map((i) => {
      return {
        id: i.id,
        statusType: VEHICLE_TYPE.FREE_NOW,
        condition: i.condition,
        coordinate: {
          latitude: i.coordinate.latitude,
          longitude: i.coordinate.longitude,
        },
        licencePlate: i.licencePlate,
        state: i.state,
      }
    })
  }
  return input.placemarks.map((i) => {
    return {
      id: i.id,
      statusType: VEHICLE_TYPE.SHARE_NOW,
      condition: i.condition,
      coordinate: {
        latitude: i.coordinates[1],
        longitude: i.coordinates[0],
      },
      licencePlate: i.licencePlate,
      address: i.address,
      state: i.state,
    }
  })
}
