import React, { useState, useEffect } from "react"
import axios from "axios"
import TableDataComponent from "./TableComponents/TableDataComponent"
import {
  Table,
  TableRow,
  TableHeaderCell,
  Pagination,
  Text,
  Button,
} from "@freenow/wave"

import {
  FreeNowDataType,
  ShareNowDataType,
  dataMapper,
  FinalType,
} from "../../mapper/Mapper"

enum SORT_STATE {
  ASC = "asc",
  DSC = "dsc",
}

export type ChildTableComponentProps = {
  props: {
    actualData: FinalType[]
    setActualData: React.Dispatch<React.SetStateAction<FinalType[]>>
  }
}

const TableMain: React.FC<ChildTableComponentProps> = (props) => {
  const [data, setData] = useState<FinalType[]>([])
  const [currentData, setCurrentData] = useState<FinalType[]>([])
  const [dataPerPage] = useState(10)
  const [currentPage, setCurrentPage] = useState(1)
  const [sortState, setSortState] = useState(SORT_STATE.ASC)

  const PAGE_SIZE = dataPerPage
  const TOTAL_ITEMS = data.length

  const lastPage = Math.ceil(TOTAL_ITEMS / PAGE_SIZE)

  // Get data from API
  const fetchData = async () => {
    const freeNowUrl = "http://localhost:5001/free-now/vehicles"
    const shareNowUrl = "http://localhost:5001/share-now/vehicles"

    const [freeNowData, shareNowData] = await Promise.all([
      axios.get<FreeNowDataType>(freeNowUrl),
      axios.get<ShareNowDataType>(shareNowUrl),
    ])

    // Get structured data from custom mapper
    const finalResult = [
      ...dataMapper(freeNowData.data),
      ...dataMapper(shareNowData.data),
    ]

    //props.props.setActualData(finalResult)
    setData(finalResult)

    // Get current page data
    const indexOfLastData = currentPage * dataPerPage
    const indexOfFirstData = indexOfLastData - dataPerPage
    setCurrentData(data.slice(indexOfFirstData, indexOfLastData))
  }

  // Fetch data and load
  useEffect(() => {
    fetchData()
    props.props.setActualData(currentData)
  }, [currentData])

  //const indexOfLastData = currentPage * dataPerPage
  //const indexOfFirstData = indexOfLastData - dataPerPage
  //const currenData = data.slice(indexOfFirstData, indexOfLastData)
  //props.props.setActualData(currenData)

  const test = () => {
    console.log(sortState)
    if (sortState === SORT_STATE.ASC) {
      setSortState(SORT_STATE.DSC)
      setData(data.sort((a, b) => b.licencePlate.localeCompare(a.licencePlate)))
    } else if (sortState === SORT_STATE.DSC) {
      setSortState(SORT_STATE.ASC)
      setData(data.sort((a, b) => a.licencePlate.localeCompare(b.licencePlate)))
    }
  }

  return (
    <div>
      <Table
        rowStyle='zebra'
        columnSpace='normal'
        rowSize='normal'
      >
        {
          <caption style={{ captionSide: "bottom", padding: "1rem" }}>
            <Pagination
              value={currentPage}
              pageSize={PAGE_SIZE}
              totalItems={TOTAL_ITEMS}
              label={
                <Text weak>
                  Page{" "}
                  <Text
                    as='b'
                    fontWeight='semibold'
                    weak
                  >
                    {currentPage} of {lastPage}
                  </Text>
                </Text>
              }
              onNextPage={() => setCurrentPage((current) => current + 1)}
              onPrevPage={() => setCurrentPage((current) => current - 1)}
              onSkipForward={() => setCurrentPage(lastPage)}
              onSkipBackward={() => setCurrentPage(1)}
            />
          </caption>
        }
        <thead>
          <TableRow>
            <TableHeaderCell scope='col'>Type</TableHeaderCell>
            <TableHeaderCell scope='col'>
              Licence Plate
              <Button onClick={test} />
            </TableHeaderCell>
            <TableHeaderCell scope='col'>Coordinates</TableHeaderCell>
            <TableHeaderCell scope='col'>Address</TableHeaderCell>
            <TableHeaderCell scope='col'>State</TableHeaderCell>
            <TableHeaderCell scope='col'>Condition</TableHeaderCell>
          </TableRow>
        </thead>
        <tbody>
          <TableDataComponent props={data} />
        </tbody>
      </Table>
    </div>
  )
}

export default TableMain
