import React from "react"
import { Divider } from "@freenow/wave"

import { Text, FreeNowArrowIcon } from "@freenow/wave"
// in the figma file logo contains text and icon

const HeaderMain = () => {
  return (
    <>
      <Text
        fontWeight='semibold'
        style={{ color: `#FF0A2B` }}
      >
        FREE
      </Text>
      <Text
        fontWeight='semibold'
        style={{ color: `#005478` }}
      >
        NOW
      </Text>
      <FreeNowArrowIcon
        size='large'
        style={{ color: `#005478` }}
      />
      <Divider />
    </>
  )
}

export default HeaderMain
