const baseScale = 0.5;
const subSpaces = [0.25, 0.5, 0.75];
export const subSpace = (spaceNumber, subSpaceValue) => `${(spaceNumber + subSpaceValue) * baseScale}rem`;
export const generateBaseSpaces = (size) => Array.from({ length: size }, (_, i) => `${baseScale * i}rem`);
export const Spaces = generateBaseSpaces(11);
