import React from "react"
import { TableRow, TableCell } from "@freenow/wave"

import { FinalType } from "../../../mapper/Mapper"

export type ChildComponentProps = {
  props: FinalType[]
}
type ClickHandler = (f: any) => (e: React.MouseEvent) => void

const TableDataComponent: React.FC<ChildComponentProps> = (data) => {
  const onClickConsole: ClickHandler = (i) => (e) => {
    e.preventDefault()
    console.log(i)
  }
  return (
    <>
      {data.props.map((item, index) => (
        <TableRow
          key={index}
          onClick={onClickConsole(item)}
        >
          <TableCell scope='row'>{item.statusType}</TableCell>
          <TableCell>{item.licencePlate}</TableCell>
          <TableCell>
            {item.coordinate.latitude}
            <> </>
            {item.coordinate.longitude}
          </TableCell>
          <TableCell>{item.address}</TableCell>
          <TableCell>{item.state}</TableCell>
          <TableCell>{item.condition}</TableCell>
        </TableRow>
      ))}
    </>
  )
}

export default TableDataComponent
